<template>
  <div class="sales-board">
    <div class="sales-board-intro">
      <h2></h2>
      <p></p>
    </div>
    <div class="sales-board-form">
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-counter @on-change="onParamChange('buyNum',$event)"></v-counter>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-selection :selections="buyTypes" @on-change="onParamChange('buyType',$event)" ></v-selection>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-chooser :selections="periodList" @on-change="onParamChange('period',$event)"></v-chooser>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          <v-m-chooser :selections="versionList" @on-change="onParamChange('versions',$event)"></v-m-chooser>
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">
          
        </div>
        <div class="sales-board-line-right">
          {{ price }} 
        </div>
      </div>
      <div class="sales-board-line">
        <div class="sales-board-line-left">&nbsp;</div>
        <div class="sales-board-line-right">
          <div class="button" @click="toShowPayDialog" >
          
        </div>
        </div>
      </div>
    </div>
    <div class="sales-board-des">
      <h2></h2>
      <p></p>

      <h3></h3>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>

      <h3></h3>
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    <my-dialog :is-show="isShowPayDailog" @on-close="closeShowPayDailog">
      <table class="buy-dialog-table">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr>
          <td>{{ buyNum }}</td>
          <td>{{ buyType.label }}</td>
          <td>{{ period.label }}</td>
          <td>
            <span v-for="item in versions">{{ item.label }}&nbsp</span>
          </td>
          <td>{{ price }}</td>
        </tr>
      </table>
      <h3 class="buy-dialog-title"></h3>
      <bank-chooser @on-change="onChangeBanks"></bank-chooser>
      <div class="button buy-dialog-btn" @click="confirmBuy">
        
      </div>
    </my-dialog>
    <my-dialog :is-show="isPayDailog" @on-close="closePayDailog"></my-dialog>
    <check-order :is-show-check-dialog="isShowCheckDialog"
                 :order-id="orderId"
                 @closeCheckDialog="closeCheckDialog"
                 >
    </check-order>
  </div>
</template>

<script>
  import BankChooser from '../../components/base/bankChooser.vue'
  import axios from 'axios'
  import _ from 'lodash'
  import MyDialog from '../../components/dialog'
  import VCounter from '../../components/base/counter'
  import VSelection from '../../components/base/selection'
  import VMChooser from '../../components/base/multiplyChooser'
  import VChooser from '../../components/base/chooser'
  import CheckOrder from '../../components/checkOrder.vue'
  export default {
    components: {
      VCounter,
      VSelection,
      VMChooser,
      VChooser,
      MyDialog,
      BankChooser,
      CheckOrder
    },
    mounted(){
      this.computAnalysisPrice()
      this.buyNum = 1
      this.buyType = this.buyTypes[0]
      this.versions = [this.versionList[0]]
      this.period = this.periodList[0]
    },
    methods:{
      toShowPayDialog(){
        this.isShowPayDailog=true
      },
      closeCheckDialog(){
                      console.log("jiancha")

        this.isShowCheckDialog=false
      },
      confirmBuy(){
        let buyVersionArray=_.map( this.versions,(item)=>{
                  return item.value
                  }
                )
        let reqParams={
          buyNumber:this.buyNum,
          buyType:this.buyType.value,
          period:this.period.value,
          version:buyVersionArray.join(','),
          bankId:this.bankId,
          price:this.price,
          product:""
        }
        axios.post('/api/createOrder',reqParams)
          .then((res)=>{
            this.orderId=res.data.orderId
        console.log(this.orderId)

        //
            this.isShowCheckDialog=true
            this.isShowPayDailog=false
          })
          .catch((error)=>{
            console.log(error)
            //?
          })
      },
      onChangeBanks(bankObj){
        this.bankId=bankObj.id
        console.log(this.bankId)
      },
      closePayDailog(){
        this.isPayDailog=false
      },
      closeShowPayDailog(){
        this.isShowPayDailog=false
      },
      onParamChange(arr,val){
        this[arr]=val
        console.log(this[arr])
        this.computAnalysisPrice()
      },
      computAnalysisPrice(){
        let buyVersionArray=_.map( this.versions,(item)=>{
          return item.value
          }
        )
        let reqParams={
          buyNumber:this.buyNum,
          buyType:this.buyType.value,
          period:this.period.value,
          version:buyVersionArray.join(',')
        }
        axios.post('api/getAnalysisPrice',reqParams)
          .then((res)=>{
            console.log(res)
            this.price=res.data.price
          })
          .catch((error)=>{
            console.log(error)
          })
      }
    },
    data () {
      return {
        isShowCheckDialog:false,
        isShowPayDailog:false,
        isPayDailog:false,
        buyNum: 1,
        buyType: {},
        versions: [],
        period: {},
        price: 0,
        versionList: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        periodList: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        buyTypes: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          },
          {
            label: '',
            value: 2
          }
        ],
        isShowPayDialog: false,
        bankId: null,
        orderId: null,
        isShowCheckOrder: false,
        isShowErrDialog: false
      }
    },

  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .smbutton {
    background: #4fc08d;
    color: #4fc08d;
    display: inline-block;
    padding: 1px 2px;
    cursor: pointer;
  }
  .buy-dialog-title {
    font-size: 16px;
    font-weight: bold;
  }
  .buy-dialog-btn {
    margin-top: 20px;
  }
  .buy-dialog-table {
    width: 100%;
    margin-bottom: 20px;
  }
  .buy-dialog-table td,
  .buy-dialog-table th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  .buy-dialog-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
</style>



